async function featuredUpdate(id,state) {
  const options = {
    headers: { 
      "Content-Type": "application/json",
    }
  };
const { data: response } = await axios.post(
  URL_PREFIX + "/v1/dashboard/service/featured/update", 
  {
    service_id: id,
    "is_featured": state.radio,
    "start_date":typeof state.start_date === "string" ? state.start_date.slice(0,10) : state.start_date.toISOString().slice(0,10),
    "end_date":typeof state.end_date === "string" ? state.end_date.slice(0,10) : state.end_date.toISOString().slice(0,10)
  },
  options
);
return response;
}